.linkSection {
    color: #FFFFFF;
    padding: 90px 0px;
    text-align: left;
}

.linkSection .label {
    font-size: 30px;
}

.linkSection.white, .linkSection.white a {
    color: #06c;
}

.linkSection.green {
    background-color: #06c;
}

.linkSection.pink {
    background-color: #ec8c3a;
}

.linkSection a {
    text-decoration: none;
    color: #FFFFFF;
    font-size: 15px;
}